// íŒŒì¼ íƒ€ì… ê²€ì¦
export function isValidImageFile(file: File): boolean {
  const validTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/webp', 'image/heic'];
  return validTypes.includes(file.type.toLowerCase());
}

// íŒŒì¼ í¬ê¸° ê²€ì¦ (ê¸°ë³¸ 10MB)
export function isValidFileSize(file: File, maxSizeMB: number = 10): boolean {
  const maxSizeBytes = maxSizeMB * 1024 * 1024;
  return file.size <= maxSizeBytes;
}

// ì˜ìˆ˜ì¦ íŒŒì¼ëª… ìƒì„±
export function generateReceiptFileName(extension: string = 'jpg'): string {
  const now = new Date();
  const year = now.getFullYear().toString().substring(2);
  const month = (now.getMonth() + 1).toString().padStart(2, '0');
  const day = now.getDate().toString().padStart(2, '0');
  const hour = now.getHours().toString().padStart(2, '0');
  const minute = now.getMinutes().toString().padStart(2, '0');
  
  return `rec${year}${month}${day}${hour}${minute}.${extension}`;
}

// Supabase Storage URLì—ì„œ íŒŒì¼ ê²½ë¡œ ì¶”ì¶œ
export function extractStoragePathFromUrl(url: string, bucketName: string = 'receipt-images'): string | null {
  try {
    const urlObj = new URL(url);
    const pathSegments = urlObj.pathname.split('/');
    const bucketIndex = pathSegments.indexOf(bucketName);
    
    if (bucketIndex === -1) return null;
    
    return pathSegments.slice(bucketIndex + 1).join('/');
  } catch {
    return null;
  }
}

// ê²€ìƒ‰ì–´ í•˜ì´ë¼ì´íŠ¸ìš© í•¨ìˆ˜
export function highlightSearchTerm(text: string, searchTerm: string): string {
  if (!searchTerm) return text;
  
  const regex = new RegExp(`(${searchTerm.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')})`, 'gi');
  return text.replace(regex, '<mark>$1</mark>');
}

// í‚¤ë³´ë“œ ì ‘ê·¼ì„±ì„ ìœ„í•œ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
export function handleKeyboardActivation(
  event: React.KeyboardEvent,
  callback: () => void
): void {
  if (event.key === 'Enter' || event.key === ' ') {
    event.preventDefault();
    callback();
  }
}