import { useState, useEffect } from 'react';
import { createClient } from '@/lib/supabase/client';
import type { ReceiptPermissions, UserRole } from '@/types/receipt';

/**
 * ì˜ìˆ˜ì¦ ê´€ë¦¬ ê¶Œí•œì„ ê´€ë¦¬í•˜ëŠ” React Hook
 * 
 * ì‚¬ìš©ìì˜ ì—­í• (role)ì— ë”°ë¼ ì˜ìˆ˜ì¦ ê´€ë ¨ ê¶Œí•œì„ ê³„ì‚°í•˜ê³  ì œê³µí•©ë‹ˆë‹¤.
 * 
 * @returns {Object} ê¶Œí•œ ì •ë³´ì™€ ê´€ë ¨ ìƒíƒœ
 * @returns {ReceiptPermissions} permissions - ê¶Œí•œ ê°ì²´
 * @returns {UserRole | null} userRole - ì‚¬ìš©ì ì—­í• 
 * @returns {boolean} loading - ê¶Œí•œ ë¡œë”© ìƒíƒœ
 * @returns {Function} refreshPermissions - ê¶Œí•œ ìƒˆë¡œê³ ì¹¨ í•¨ìˆ˜
 * 
 * @example
 * ```tsx
 * const { permissions, userRole, loading } = useReceiptPermissions();
 * 
 * if (loading) return <div>ë¡œë”© ì¤‘...</div>;
 * 
 * return (
 *   <div>
 *     {permissions.canUpload && <UploadButton />}
 *     {permissions.canDelete && <DeleteButton />}
 *   </div>
 * );
 * ```
 */
export function useReceiptPermissions() {
  const [permissions, setPermissions] = useState<ReceiptPermissions>({
    canView: false,
    canUpload: false,
    canDownload: false,
    canPrint: false,
    canDelete: false,
    canViewUploaderInfo: false,
  });
  const [userRole, setUserRole] = useState<UserRole | null>(null);
  const [loading, setLoading] = useState(true);

  const supabase = createClient();

  const checkPermissions = async () => {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        setLoading(false);
        return;
      }

      const { data: employee } = await supabase
        .from('employees')
        .select('purchase_role')
        .eq('email', user.email)
        .single();

      const role = employee?.purchase_role || '';
      setUserRole(role);

      // ê¶Œí•œ ê³„ì‚°
      const isAppAdmin = role.includes('app_admin');
      const isHr = role.includes('hr');
      const isLeadBuyer = role.includes('lead buyer');
      
      // ì˜ìˆ˜ì¦ ê´€ë¦¬ ì ‘ê·¼ ê°€ëŠ¥ ê¶Œí•œ
      const hasReceiptAccess = isAppAdmin || isHr || isLeadBuyer;

      setPermissions({
        canView: hasReceiptAccess,
        canUpload: hasReceiptAccess,
        canDownload: hasReceiptAccess,
        canPrint: hasReceiptAccess,
        canDelete: isAppAdmin, // ì˜¤ì§ app_adminë§Œ
        canViewUploaderInfo: isAppAdmin, // ì˜¤ì§ app_adminë§Œ
      });
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    checkPermissions();
  }, []);

  return {
    permissions,
    userRole,
    loading,
    refreshPermissions: checkPermissions,
  };
}